@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,800&family=Permanent+Marker&family=Raleway:wght@200;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,800&family=Patua+One&family=Permanent+Marker&family=Raleway:wght@200;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Gothic+A1:wght@300;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,800&family=Patua+One&family=Permanent+Marker&family=Raleway:wght@200;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Desc {
    text-align: left;
    padding: 150px 0px 100px 50px;
    letter-spacing: 1px;
    font-size: 20px;
}

.boldDesc {
    font-weight: 800;
    font-size: 30px;
    font-family: 'Raleway', sans-serif;
}

.subDesc {
    font-family: 'Raleway', sans-serif;
    line-height: 200%;
}
.name {
    font-size: 80px;
    font-weight: 900;
    font-family: 'Raleway', sans-serif;
}

.Imac {
    background-image: none;
}

.scroll {
    padding: 80px;
    font-family: 'Raleway', sans-serif;
    text-align: center;
    color: rgb(97, 97, 204);
}

img {
    width: 20px;
    height: 20px;
}
@media (min-width: 600px) {
    .tools {
        display: flex;
        padding: 10px;
        color: #94b3e3;
        text-align: center;
    }

    .tools:hover {
        background-color: #94b3e3;
        color: white;
        transition: .5s;
    }

    .tool {
        flex: 1 1 auto;
        font-family: 'Patua One', cursive;
        padding: 10px;
        margin-bottom: 60px;
    }

    .part {
        font-size: 30px;
        font-weight: 600;
        margin-top: 60px;
        margin-bottom: 30px;
    }
}

@media (max-width: 600px) {
    .tools {
        padding: 10px;
        color: #94b3e3;
        text-align: center;
    }

    .tools:hover {
        background-color: #94b3e3;
        color: white;
        transition: .5s;
    }

    .tool {
        font-family: 'Patua One', cursive;
        padding: 10px;
        margin-bottom: 60px;
    }

    .part {
        font-size: 30px;
        font-weight: 600;
        margin-top: 60px;
        margin-bottom: 30px;
    }
}
.Projects {
    padding: 10px;
    margin-top: 100px;
    position: absolute;
    font-family: 'Gothic A1', sans-serif;
}

.name {
    font-size: 50px;
    padding: 10px;
}

.left {
    position: relative;
    left: 20px;
    padding-bottom: 50px;
    padding-right: 80px;
    margin-right: 30px;
}

.left:hover {
    background-color: #94b3e3;
    color: white;
    transition: 0.5s;
}

.right {
    position: relative;
    left: 20px;
    text-align: right;
    margin-right: 30px;
}

.right:hover {
    background-color: #94b3e3;
    color: white;
    transition: 0.5s;
}

.project {
    display: flex;
}

.AcidRain {
    width: 700px;
    height: 500px;
    flex: 1 1 auto;
}

.misae {
    width: 300px;
    height: 700px;
    text-align: right;
}

.projectDesc {
    flex: 1 1 auto;
    padding-left: 20px;
    padding-right: 20px;
    font-weight: 900;
}

.resume {
    font-size: 20px;
    color: white;
    font-weight: 300;
}

.projectIntro {
    font-size: 20px;
    color: #3164af;
}

.resumebox {
    padding-top: 80px;
}
.contacts {
    display: flex;
    margin-top: 1600px;
    padding: 30px;
    background-color: #94b3e3;
}

.contact {
    flex: 1 1 auto;
    text-align: center;
    text-decoration: none;
    color: white;
}

a:hover {
    color: #0367fc;
    transition: color .3s;
}
.navBox {
    padding: 30px;
}

.before {
    position: fixed;
    left: 30px;
}

.before:hover {
    color: royalblue;
    transition: .5s;
}

.email {
    position: fixed;
    right: 30px;
}

.email:hover {
    color: royalblue;
    transition: .5s;
}
.Modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
}

.ModalBox {
    width: 480px;
    height: 250px;
    background-color: white;
    position: relative;
    box-sizing: border-box;
    margin: 50px auto;
    padding: 10px;
    background: #fff;  
}

.close {
    float: right;
    font-size: 30px;
    padding-right: 10px;
}

.desc {
    padding-top: 40px;
    color: gray;
    font-size: 15px;
}

input {
    margin-top: 30px;
    border-radius: 2px;
    width: 100%;
    height: 40px;
    border: 1px solid #e5e5e5;
    padding: 9px 12px;
    outline: none;
    box-sizing: border-box;
    color: #999999;
}

button {
    height: 40px;
    font-size: 14px;
    padding: 20px 30px;
    cursor: pointer;
    background-color: black;
    color: white;
    line-height: 1px;
    margin-top: 30px;
    margin-bottom: 12px;
    border-radius: 5px;
    border-style: none;
}
.toggle {
    background-color: #94b3e3;
    padding: 30px;
    line-height: 280%;
    color: white;
    border-radius: 20%;
}

.career {
    font-size: 50px;
    font-weight: 300;
    color: royalblue;
    text-align: center;
    font-family: 'Patua One', cursive;
}

.company {
    font-family: 'Patua One', cursive;
    font-size: 30px;
    text-align: center;
}

.period {
    font-family: 'Raleway', sans-serif;
    font-size: 20px;
    text-align: center;
}
